.Button {
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  cursor: pointer;
  font: inherit;
  padding: 10px;
  margin: 10px;
  font-weight: bold;

  &:first-of-type {
    margin-left: 0;
    padding-left: 0;
  }

  &.Success {
    color: #5c9210;
  }

  &.Danger {
    color: #944317;
  }

  &.Primary {
    color: #0984e3;
  }
}
